.marker {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 18px;
    height: 18px;
    background-color: #000;
    border: 2px solid #fff;
    border-radius: 100%;
    user-select: none;
    transform: translate(-50%, -50%);
    &:hover {
      z-index: 1;
    }
  }
  .circle {
    background: #d83b01;
    border-radius: 50%;
    color: #fff;
    height: 2.5em;
    position: relative;
    width: 2.5em;
  
    border: 1px solid transparent;
  }
  
  .circleText {
    text-align: center;
    height: 50%;
    left: 50%;
    position: absolute;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 50%;
  }
  .DashboardMap{
    width: 100%;
    height: calc(100vh - 430px);
  }
  .TrackerMap {
    width: 100%;
    height: 60vh;
  }

  .SlideBar{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    // align-items: space-around;
    margin-top:10px;
    padding:10px;
    height:100px;
  }